* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
}
html,
body {
  overflow: hidden;
}

#control {
  position: fixed;
  bottom: 0;
  display: flex;
  width: 100%;
  height: 100px;
}
.button {
  flex: 1;
}
#first {
  background-color: #344f73;
}
#second {
  background-color: #f2b705;
}
#third {
  background-color: #8c591b;
}
#fourth {
  background-color: #f25c05;
}
#gameover {
  position: fixed;
  top: 30%;
  width: 100%;
  height: 100px;
  background-color: rgba(255, 255, 255, 0.8);
  display: none;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  z-index: 10;
}
